import React, { Component } from "react";
import { connect } from "react-redux";
import { generationActionCreator } from "../actions/generation";

const DEFAULT_GENERATION = { generationId: "", expiration: "" };
const MINIMUN_DELAY = 3000;

class Generation extends Component {
  state = { generation: DEFAULT_GENERATION };

  timer = null;

  componentDidMount() {
    this.fetchNextGeneration();
  }

  componentWillUnmount() {
    cleanTimeout(this.timer);
  }

  fetchGeneration = () => {
    fetch("http://localhost:3000/generation")
      .then((response) => response.json())
      .then((json) => {
        //console.log("json", json);
        //this.setState({ generation: json.generation });

        this.props.dispatchGeneration(json.generation);
      })
      .catch((error) => console.error("error", error));
  };

  fetchNextGeneration = () => {
    this.fetchGeneration();

    let delay =
      new Date(this.props.generation.expiration).getTime() - new Date().getTime;

    if (delay < MINIMUN_DELAY) {
      delay = MINIMUN_DELAY;
    }

    this.timer = setTimeout(() => this.fetchNextGeneration(), delay);
  };

  render() {
    console.log(this.props);
    //console.log("");
    const { generation } = this.props;
    return (
      <div>
        <h3>Generation {generation.generationId}. Expires on:</h3>
        {/* <!-- .toString()new Date(generation.expiration)render --> */}
        <h4>{new Date(generation.expiration).toString()}</h4>
      </div>
    );
  }
}

// storestate
const mapStateToProps = (state) => {
  const generation = state.generation;

  return { generation };
};

